.hero {
    position: relative;
    height: 100vh;
    background-color: #111827;
    background-image: url('https://r2.sportlife.ua/083_A7363_Edit_kopiya_46d6ba74de.jpg');
    background-size: cover;
    background-position: center center;
    color: white;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding-left: 40px;
    padding-right: 40px;
    z-index: 1;
}       
.hero::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.55); 
    box-shadow: inset 0 0 200px rgba(0, 0, 0, 0.8); 
    z-index: 2;
    pointer-events: none;
}
.heroContent {
    position: relative;
    z-index: 3;
    max-width: 480px;
}
.heroContent h2 {
    font-size: 1.8rem;
    font-weight: 600;
    margin-bottom: 10px;
    letter-spacing: 2px;
    text-transform: uppercase;
    color: #f9f9f9;
    opacity: 0.85;
}
.heroContent h1 {
    font-size: 4rem;
    margin: 0;
    font-weight: 800;
    line-height: 1.1;
    color: #fff;
}


@media (max-width: 768px) {
    .hero {
        padding-left: 20px;
        padding-right: 20px;
        justify-content: center; 
    }
    .heroContent {
        max-width: 100%;
        text-align: center;
        padding: 0 10px;
    }
    .heroContent h2 {
        font-size: 22px; 
        margin-bottom: 8px;
        letter-spacing: 1.5px;
    }
    .heroContent h1 {
        font-size: 45px;
        line-height: 1.1;
        margin-bottom: 20px;
    }
}
